import { VuexModule, Module, Mutation, Action, getModule } from 'vuex-module-decorators';
import store from '@/store';
import { AlertsModule } from './alerts';
import { VkModule, vkApi } from '@/store/modules/vk';

let WebSocket = null;
const requests = {};
let id = 0;

export interface IWebsocketState {
}

@Module({ dynamic: true, store, name: 'websocket' })
class Websocket extends VuexModule implements IWebsocketState {
	_isConnected: boolean = false;


	@Action({ rawError: true })
	Init(payload) {
		const { eventListener } = payload;
		eventListener.onopen = async (data) => {
			try {
				WebSocket = data.target;
				this.context.commit('SET_IS_CONNECTED', true);

				if (this.userRegistered || await this.registerUser()) {
					const isAuth = await this.authUser();
					if(isAuth){
						console.log('авторизованы')
					}
				}
			} catch (e) {
				console.error(e);
			}
		};
		eventListener.onclose = (data) => {
			this.context.commit('SET_IS_CONNECTED', false);
		};
		eventListener.onmessage = (message) => {
			console.log('onmessage', message)
			const data = message.data;
			let response = undefined;
			if (typeof data === 'object') {
				response = data;
			} else if (data.charAt(0) === '{') {
				try {
					response = JSON.parse(data);
				} catch (e) {
					console.error(e);
					return false;
				}
			} else {
				console.log(data);
				return false;
			}


			if (response.hasOwnProperty('result')) {
				if (response.hasOwnProperty('id')) {
					this.onResponse(response);
				} else {
					this.onResponseWithoutId(response);
				}
			} else if (response.hasOwnProperty('error')) {
				this.onResponseError(response);
			} else {
				console.log('strange response', response);
			}

		};

		eventListener.onerror = (error) => {
			if(this.isConnected){
				console.log('onerror', error);
			}
		};
	}

	get isConnected() {
		return this._isConnected;
	}

	get userRegistered() {
		return localStorage.getItem('userRegistered');
	}

	@Mutation
	SET_IS_CONNECTED(val: boolean) {
		this._isConnected = val;
	}

	@Action({ rawError: true })
	sendText(text) {
		if (this.isConnected && WebSocket) {
			WebSocket.send(text);
		}
	}

	@Action({ rawError: true })
	sendObject(obj) {
		if (this.isConnected && WebSocket) {
			WebSocket.sendObj(obj);
		}
	}

	@Action({ rawError: true })
	serverMethod(obj) {
		const { params } = obj;
		id = id + 1;
		obj['jsonrpc'] = '2.0';
		obj['id'] = id;
		return new Promise((resolve, reject) => {
			try {
				if (this.isConnected) {
					WebSocket.sendObj(obj);
					console.log('WebSocket.sendObj',JSON.stringify(obj))
					requests[id] = { params, resolve, reject };
				} else {
					reject({ 'code': -1, 'message': 'WebSocket not connected' });
				}
			} catch (e) {
				reject(e);
			}
		});
	}

	@Action({ rawError: true })
	onResponse(obj) {
		const tempObj = requests[obj.id];
		if (tempObj && tempObj.resolve) {
			try {
				tempObj.resolve(obj);
				delete requests[obj.id];
			} catch (e) {
				console.error(e);
			}
		} else {
			console.error('onResponse wrong id', obj.id);
		}

	}

	@Action({ rawError: true })
	onResponseError(obj) {
		console.log('onResponseError', JSON.stringify(obj));
	}

	@Action({ rawError: true })
	onResponseWithoutId(obj) {
		console.log('onResponseWithoutId', obj);
	}

	@Action({ rawError: true })
	async registerUser() {
		try {
			const paramsForRegister = await VkModule.getParamsForRegister();
			console.log('paramsForRegister', paramsForRegister);
			this.serverMethod(paramsForRegister)
				.then(data => {
					console.log('registerUser', data)
					localStorage.setItem('userRegistered', '1')
					return true
				})
				.catch(e => {
					console.error('registerUser error', e)
					localStorage.removeItem('userRegistered')
					return false
				});
		} catch (e) {
			console.error(e);
			return false
		}
	}

	@Action({ rawError: true })
	async authUser() {
		try {
			const paramsForAuth = await VkModule.getParamsForAuth();
			// console.log('paramsForAuth', paramsForAuth);
			this.serverMethod(paramsForAuth)
				.then(data => {
					console.log('AuthUser', data); debugger
					return true
				})
				.catch(e => {
					console.error('AuthUser error', e)
					return false
				});
		} catch (e) {
			console.error(e);
			return false
		}
	}

}

export const WebsocketModule = getModule(Websocket);
