import { VuexModule, Module, Mutation, Action, getModule } from 'vuex-module-decorators';
import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';
import bridgeMock from '@vkontakte/vk-bridge-mock';

const vk_Api = inFrame() ? new VKMiniAppAPI() : new VKMiniAppAPI(bridgeMock);
vk_Api.initApp();
export const vkApi = vk_Api

import { detectIsMobile } from './_utils';
import { MD5 } from '@/lib/md5';
import store from '@/store';

const api_secret = 'PswZKFiEQ8gS0vsa7nLa';

export interface IVkState {
}

@Module({ dynamic: true, store, name: 'app' })
class Vk extends VuexModule implements IVkState {
	mobile = detectIsMobile();

	vk_app_id = 7350550
	_vk_settings = {
		vk_app_id: 0,
		vk_user_id: 0,
		vk_platform: '',
		viewer_id: -1
	};
	_auth_key = '';

	get auth_key() {
		return this._auth_key;
	}

	get isMobile() {
		return this.mobile;
	}

	get vkSettings() {
		return this._vk_settings;
	}

	get appUrl() {
		return `/app${this._vk_settings.vk_app_id}_${this._vk_settings.vk_user_id}`;
	}

	@Mutation
	setVkSettings(payload) {
		this._vk_settings = payload;
		// this._auth_key = MD5(this._vk_settings.vk_app_id + '_' + this._vk_settings.vk_user_id + '_' + api_secret);
	}

	@Mutation
	setAuthKey(payload){
		this._auth_key = payload
	}

	@Action({ rawError: true })
	async getParamsForRegister() {
		const userInfo = await vkApi.getUserInfo();
		const tempStr = this.vk_app_id.toString()+ '_' +userInfo.id.toString()+  '_' + api_secret
		console.log('tempStr',tempStr)
		const auth_key = MD5(tempStr);
		console.log('auth_key', auth_key)
		this.context.commit('setAuthKey', auth_key);

		return new Promise((resolve, reject)=>{

			const obj = {
				'method': 'auth.register_vk_bridge_user',
				'params': {
					'viewer_id': userInfo.id,
					'auth_key': auth_key,
					'nickname': 'VK:'+userInfo.id,
					'profile': {
						'first_name': userInfo.first_name,
						'last_name': userInfo.last_name,
						'city': userInfo.city,
						'country': userInfo.country,
						'sex': userInfo.sex,
						'photo_100': userInfo.photo_100,
						'photo_200': userInfo.photo_200,
					},
					"vk_profile": {"vk_platform": this.vkSettings.vk_platform}
				}
			};
			resolve(obj)
		})
	}

	@Action({ rawError: true })
	async getParamsForAuth() {
		const userInfo = await vkApi.getUserInfo();
		const auth_key = MD5(this._vk_settings.vk_app_id + '_' + userInfo.id + '_' + api_secret);
		this.context.commit('setAuthKey', auth_key);

		return new Promise((resolve, reject)=>{
			const obj = {
				'method': 'auth.auth_vk_bridge_user',
				'params': {
					'viewer_id': userInfo.id,
					'auth_key': auth_key,
				}
			};
			resolve(obj)

		})
	}
}

export const VkModule = getModule(Vk);


function inFrame() {
	try {
		return window.self !== window.top;
	} catch (e) {
		return true;
	}
}
